.home-title {
  margin-top: 200px;
  margin-bottom: 20px;
  text-align: center;
  color: lightgray;
}

a.home-link {
  color: lightgray;
}

a.home-link:hover {
  background-color: #00A79D;
}

.list-title, .story-title {
  margin-top: 20px;
  margin-bottom: -10px;
  text-align: center;
}

.display-5 {
  font-size: 2.5rem;
  font-weight: 300;
  line-height: 1.2;
}

.display-6 {
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.2;
}

.display-7 {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.2;
}

.card {
  border-radius: 0px;
}

pre {
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 5px;
}

pre code {
  color: #2f6f9f;
}

header {
  min-height: 20rem;
  width: 100%;
  position: relative;
  border-top: .3rem solid;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
  color: white;
  background: linear-gradient(rgba(0, 49, 82, 0.65), rgba(0, 49, 82, 0.65)), url(/image/header.jpg) no-repeat center;
  background-size: cover;
  border-color: #00A79D;
  background-color: #003152;
}

header .container {
  padding-top: 50px;
}

.disclaimer {
  font-size: 0.85rem;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
  background-color: #003152;
  color: white;
  margin-top: -10px;  
}

.disclaimer .nav-pills>li>a {
	border-radius: 0;
	color: #fff;
}

.disclaimer .nav-pills>li>a:hover {
	background-color: #00A79D;
}

.disclaimer .nav-pills .nav-link.active {
	background-color:	#4682B4;
}

.storydiv img {
  max-width: 100%;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5) !important;
}

.list-group a {
  padding: 8px 10px;
}

.list-group-item.active {
  background-color: #5bc0de;
  border-color: #5bc0de;
}
